import React, { Component } from 'react'
import TagsPaper from './tagsPaper'
import CurrentTagPaper from './currentTagPaper'
import ApiCall from './Backend/apiCall';

export default class Tags extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tags: [],
            toUpdate: true,
        };
    }
    getTags = (e) => {
        
        const array = e;
        
        return array
    }
    render() {

        return (
            <div >
                <p>
                    Get recommendations for articles that matches your
                    interests when you follow more tags:
                </p>
                <b>
                    You are following
                </b>

                <TagsPaper></TagsPaper>
                <b>
                    From this article
                </b>
                {/* pass Tag array name here*/}
                <CurrentTagPaper tagArray={<ApiCall getTags={this.getTags} />} />
                
            </div>
        )
    }
}
